<template>
  <div class="complete">
    <div class="complete__container">
      <h2 class="complete__title">{{ congra.substring(0, textleng) }}</h2>
      <div :class="textleng >= leng ? 'complete__img show' : 'complete__img'">
        <img :src="require('@/assets/pics/' + scrResult + '.jpg')" ref="photo" />
      </div>
      <p :class="textleng >= leng ? ' show' : ''" class="complete__tips" @click="downloadImg"></p>
      <router-link :class="textleng >= leng ? ' show' : ''" class="complete__start" to="/">RESTART</router-link>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import html2canvas from 'html2canvas'
export default {
  name: 'Complete',
  data() {
    return {
      textleng: 0,
      leng: 0,
      congra: 'Congrats! This gift is for you.',
      scrResult: '1'
    }
  },
  mounted() {
    let result = this.answersGetter.join('')
    switch (result) {
      case '000':
        this.congra = ''
        this.scrResult = 1
        break
      case '001':
        this.congra = ''
        this.scrResult = 2
        break
      case '010':
        this.congra = ''
        this.scrResult = 3
        break
      case '011':
        this.congra = ''
        this.scrResult = 4
        break
      case '100':
        this.congra = '69'
        this.scrResult = 5
        break
      case '101':
        this.congra = ''
        this.scrResult = 6
        break
      case '110':
        this.congra = ''
        this.scrResult = 7
        break
      case '111':
        this.congra = ''
        this.scrResult = 8
        break
      default:
        this.congra = ''
        this.scrResult = 1
        break
    }
    this.leng = this.congra.length
    for (let i = 0; i < this.leng; i++) {
      setTimeout(() => {
        this.textleng++
      }, i * 200)
    }
  },
  computed: {
    ...mapGetters(['answersGetter'])
  },
  methods: {
    downloadImg() {
      html2canvas(this.$refs.photo, {
        backgroundColor: null, // 
        dpi: 192, // ,96
        scale: 2, // 
        useCORS: true // CORS
      }).then((canvas) => {
        var dataUrl = canvas.toDataURL()
        var newImg = document.createElement('a')
        newImg.href = dataUrl
        newImg.setAttribute('download', 'download.png')
        document.querySelector('body').append(newImg)
        newImg.click()
        newImg.remove()
      })
    }
  }
}
</script>
